.logo.is-animetion {
  margin-top: 0.6em;
}

.logo.is-animetion span {
  display: inline-block;
  animation: wave-text 1s ease-in-out infinite;
}

.logo.is-animetion {
  span:nth-of-type(1){ animation-delay: 0.0s; }
  span:nth-of-type(2){ animation-delay: 0.1s; }
  span:nth-of-type(3){ animation-delay: 0.2s; }
  span:nth-of-type(4){ animation-delay: 1.3s; }
  span:nth-of-type(5){ animation-delay: 0.4s; }
  span:nth-of-type(6){ animation-delay: 0.5s; }
  span:nth-of-type(7){ animation-delay: 0.6s; }
  span:nth-of-type(8){ animation-delay: 1.7s; }
  span:nth-of-type(9){ animation-delay: 0.8s; }
  span:nth-of-type(10){ animation-delay: 0.9s; }
  span:nth-of-type(11){ animation-delay: 1.0s; }
  span:nth-of-type(12){ animation-delay: 1.1s; }
  span:nth-of-type(13){ animation-delay: 1.2s; }
  span:nth-of-type(14){ animation-delay: 1.3s; }
  span:nth-of-type(15){ animation-delay: 0.4s; }
  span:nth-of-type(16){ animation-delay: 0.5s; }
  span:nth-of-type(17){ animation-delay: 0.6s; }
  span:nth-of-type(18){ animation-delay: 0.7s; }
  span:nth-of-type(19){ animation-delay: 0.8s; }
  span:nth-of-type(20){ animation-delay: 1.9s; }
  span:nth-of-type(21){ animation-delay: 0.1s; }
  span:nth-of-type(22){ animation-delay: 0.2s; }
  span:nth-of-type(23){ animation-delay: 1.3s; }
  span:nth-of-type(24){ animation-delay: 1.4s; }
}

@keyframes wave-text{
  00%{
    transform: translateY(0em);
  }
  60%{
    transform: translateY(-0.6em);
  }
  100%{
    transform: translateY(0em);
  }
}













.welcometext {
  letter-spacing: -3px;
  transition: 700ms ease;
  font-variation-settings: "wght" 311;
  margin-bottom: 0.8rem;
  font-family: 'Gambetta', serif;
  outline: none;
  text-align: center;
  letter-spacing: -3px;
  animation-name: wavy;
  animation-duration: 1.3s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  animation: floating 3s linear infinite;
  margin: 3.5rem auto;
  padding: 1rem;
  background: #01796f;
  color: #ffffff;
  box-shadow: -20px -17px 8px 0px #5a9bad63;
  border-radius: 10px;
}
@keyframes floating {
  0% {
      transform: translate(0%, 30%);
  }
  50% {
      transform: translate(0%, -30%);
  }
  100% {
      transform: translate(0%, 30%);
  }
}
@keyframes wavy {
  0% {
    top: 0px;
  }
  50% {
    top: -15px;
  }
  100% {
    top: 0px;
  }
}
.welcometext:hover {
  font-variation-settings: "wght" 582; 
  letter-spacing: 1px;
  cursor: grab;
}










